import { Container, Title, Text, Stack, Group, Badge, Breadcrumbs, Anchor } from '@mantine/core';
import { useParams, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';

const mockArticle = {
  id: 1,
  title: '    ',
  category: '',
  date: '2024-03-20',
  tags: ['', ''],
  content: `
    <h2>  </h2>
    <p>        :</p>
    <ul>
      <li>   </li>
      <li>    </li>
      <li>    </li>
    </ul>
    <h2>   </h2>
    <p>    :</p>
    <ol>
      <li>  </li>
      <li>  </li>
      <li>   </li>
    </ol>
  `,
};

export default function Article() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [article] = useState(mockArticle);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // TODO:    
    setLoading(false);
  }, [id]);

  if (loading) {
    return (
      <Container size="sm" py="xl">
        <Text>...</Text>
      </Container>
    );
  }

  return (
    <Container size="sm" py="xl">
      <Stack gap="xl">
        <Breadcrumbs>
          <Anchor onClick={() => navigate('/home')}></Anchor>
          <Anchor onClick={() => navigate('/categories')}></Anchor>
          <Anchor onClick={() => navigate(`/category/${article.category}`)}>
            {article.category}
          </Anchor>
          <Text>{article.title}</Text>
        </Breadcrumbs>

        <Stack gap="md">
          <Title order={1}>{article.title}</Title>
          <Group gap="xs">
            <Badge variant="light" color="blue">
              {article.category}
            </Badge>
            {article.tags.map((tag) => (
              <Badge key={tag} variant="light">
                {tag}
              </Badge>
            ))}
          </Group>
          <Text size="sm" c="dimmed">
            {new Date(article.date).toLocaleDateString()}
          </Text>
        </Stack>

        <div dangerouslySetInnerHTML={{ __html: article.content }} />
      </Stack>
    </Container>
  );
} 