import { Container, Title, Text, Stack, Group, Card, Badge, Breadcrumbs, Anchor, TextInput } from "@mantine/core";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { IconSearch } from "@tabler/icons-react";

const mockArticles = [
  {
    id: 1,
    title: "    ",
    category: "",
    date: "2024-03-20",
    tags: ["", ""],
  },
  {
    id: 2,
    title: "    ",
    category: "",
    date: "2024-03-19",
    tags: ["", ""],
  },
  {
    id: 3,
    title: "  ",
    category: "",
    date: "2024-03-18",
    tags: ["", ""],
  },
];

export default function Search() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [searchQuery, setSearchQuery] = useState(searchParams.get("q") || "");
  const [tagFilter] = useState(searchParams.get("tag") || "");
  const [articles] = useState(mockArticles);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // TODO:    
    setLoading(false);
  }, [searchQuery, tagFilter]);

  if (loading) {
    return (
      <Container size="sm" py="xl">
        <Text>...</Text>
      </Container>
    );
  }

  return (
    <Container size="sm" py="xl">
      <Stack gap="xl">
        <Breadcrumbs>
          <Anchor onClick={() => navigate("/home")}></Anchor>
          <Text></Text>
        </Breadcrumbs>

        <Title order={1}></Title>

        <TextInput
          placeholder="..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          leftSection={<IconSearch size={16} />}
        />

        {tagFilter && (
          <Group gap="xs">
            <Text>:</Text>
            <Badge variant="light" color="blue">
              {tagFilter}
            </Badge>
          </Group>
        )}

        <Stack gap="md">
          {articles.map((article) => (
            <Card
              key={article.id}
              withBorder
              p="md"
              radius="md"
              style={{ cursor: "pointer" }}
              onClick={() => navigate(`/article/${article.id}`)}
            >
              <Stack gap="xs">
                <Text fw={500}>{article.title}</Text>
                <Group gap="xs">
                  <Badge variant="light" color="blue">
                    {article.category}
                  </Badge>
                  {article.tags.map((tag) => (
                    <Badge key={tag} variant="light">
                      {tag}
                    </Badge>
                  ))}
                </Group>
                <Text size="sm" c="dimmed">
                  {new Date(article.date).toLocaleDateString()}
                </Text>
              </Stack>
            </Card>
          ))}
        </Stack>
      </Stack>
    </Container>
  );
} 