import { Container, Title, Text, Stack, Group, Badge, Breadcrumbs, Anchor } from '@mantine/core';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';

//    
const mockTags = [
  { id: 1, name: '', count: 5 },
  { id: 2, name: '', count: 3 },
  { id: 3, name: '', count: 4 },
  { id: 4, name: '', count: 2 },
  { id: 5, name: '', count: 6 },
];

export default function Tags() {
  const navigate = useNavigate();
  const [tags] = useState(mockTags);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // TODO:    
    setLoading(false);
  }, []);

  if (loading) {
    return (
      <Container size="sm" py="xl">
        <Text>...</Text>
      </Container>
    );
  }

  return (
    <Container size="sm" py="xl">
      <Stack gap="xl">
        <Breadcrumbs>
          <Anchor onClick={() => navigate('/home')}></Anchor>
          <Text></Text>
        </Breadcrumbs>

        <Title order={1}></Title>

        <Group gap="md">
          {tags.map((tag) => (
            <Badge
              key={tag.id}
              size="lg"
              variant="light"
              style={{ cursor: 'pointer' }}
              onClick={() => navigate(`/search?tag=${tag.name}`)}
            >
              {tag.name} ({tag.count})
            </Badge>
          ))}
        </Group>
      </Stack>
    </Container>
  );
} 